import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate } from "../../../../components/helpers/field";
import { getTglSystem } from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import ValidasiTanggalLaporan from "../../../../Validasi/ValidasiTanggalLaporan";
import Loading from "react-fullscreen-loading";
import LaporanTitipUangPdf from "./pdf/LaporanTitipUangPdf";
import LaporanTitipUtangExel from "./exel/LaporanTitipUtangExel";
class HeadLaporanTitipUang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      columns: [
        {
          dataField: "nama_barang",
          text: "Nama Barang",
          footer: "",
          footerAttrs: {
            colSpan: "5",
          },
        },

        {
          dataField: "berat",
          text: "Berat",
          headerClasses: "text-right",
        },
        {
          dataField: "harga_jual",
          text: "Harga Jual",
          headerClasses: "text-right",
        },
        {
          dataField: "ongkos",
          text: "Ongkos",
          headerClasses: "text-right",
        },

        {
          dataField: "harga_total",
          text: "Sub Total",
          headerClasses: "text-right",
        },
        {
          dataField: "dp_rp",
          text: "Dp",
          headerClasses: "text-right",
          formatter: (data) => {
            return (
              <div className="text-right"> {data.toLocaleString("kr-KO")}</div>
            );
          },
          footer: (columnData) => (
            <div className="text-right">
              {" "}
              {columnData
                .reduce((acc, item) => acc + item, 0)
                .toLocaleString("kr-KO")}{" "}
            </div>
          ),
        },
      ],
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  exportPdf() {
    LaporanTitipUangPdf(this.props.LaporanTitipUang);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>

          <div className="col-lg-4">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-lg-12">
            <Tabel
              keyField="berat_bruto"
              data={this.props.export ? this.props.LaporanTitipUang : []}
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Laporan Titip Uang Kosong"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-6">
                <button
                  type="button"
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                <LaporanTitipUtangExel
                  data={this.props.LaporanTitipUang}
                />
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

HeadLaporanTitipUang = reduxForm({
  form: "HeadLaporanTitipUang",
  enableReinitialize: true,
  validate: ValidasiTanggalLaporan,
})(HeadLaporanTitipUang);
// export default connect()(HeadLaporanTitipUang);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadLaporanTitipUang);
