import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  getData2,
  formatDate,
  moment,
  ToastNotification,
  PanelBody,
  localStorageencryp,
} from "../../../../components/helpers/library";
import HeadLaporanTitipUang from "./HeadLaporanTitipUang";

class LaporanTitipUang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      LaporanTitipUang: [],
      export: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
    };
    this.setState({
      isLoading: true,
    });
    getData2(
      "titip/report-titip-uang?tgl_from=" +
        data_post.tgl_awal +
        "&tgl_to=" +
        data_post.tgl_akhir
    )
      .then((res) => {
        // console.log(res);
        if (res.data.length === 0) {
          ToastNotification("info", "Laporan Titip Uang Kosong");
          this.setState({
            export: false,
            isLoading: false,
          });
          return false;
        }
        ToastNotification("success", "Laporan Titip Uang Tersedia");
        localStorageencryp("tgl_laporan", JSON.stringify(data_post));

        this.setState({
          LaporanTitipUang: res.data,
          isLoading: false,
          export: true,
        });
      })
      .catch((err) => {
        ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
        this.setState({
          export: false,
          isLoading: false,
        });
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Titip Uang</li>
        </ol>
        <h1 className="page-header">Laporan Titip Uang </h1>
        <Panel>
          <PanelHeader>Laporan Titip Uang</PanelHeader>
          <br />
          {/* Blank Page */}
          <PanelBody>
            <HeadLaporanTitipUang
              export={this.state.export}
              isLoading={this.state.isLoading}
              LaporanTitipUang={this.state.LaporanTitipUang}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
          <br />
          {/* End Tambah Blank Page  */}
        </Panel>
      </div>
    );
  }
}

LaporanTitipUang = reduxForm({
  form: "LaporanTitipUang",
  enableReinitialize: true,
})(LaporanTitipUang);
export default connect(null)(LaporanTitipUang);
